@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";
@import "vendor/libs";
@import "base/fonts";
@import "base/general";
@import "../blocks/modules";



html, body {
  font-size: 16px;
  @media (max-width: 48rem) {
    font-size: 14px;
  }
  @media (max-width: 30rem) {
    font-size: 13px;
  }

  @media (max-width: 25rem) {
    font-size: 12px;
  }
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

h1 {
  font-family: 'Roboto Slab', serif;
  font-size: 3rem;
  line-height: 2.75rem;
  letter-spacing: 0.1rem;
  font-weight: 700;
  @media (max-width: 25rem) {
    font-size: 2rem;
  }
}

h4 {
  font-family: 'Roboto Slab', serif;
  font-size: 1.12rem;
  font-weight: 400;
  line-height: 2.75rem;
  letter-spacing: 0.057rem;
  color: #65e496;
  @media (max-width: 25rem) {
    font-size: 1rem;
    line-height: 1.5rem;
  }
}

p {
  font-family: 'Open Sans', sans-serif;
  font-size: 0.94rem;
  letter-spacing: 0.045rem;
  line-height: 1.5rem;
  font-weight: 400;
  @media (max-width: 25rem) {
    font-size: 0.8rem;
    line-height: 1.2rem;
  }
}

small {
  font-family: 'Open Sans', sans-serif;
  font-style: italic;
  color: #ffffff;
  font-size: 0.75rem;
  letter-spacing: 0.035rem;
}

.btn {
  border: none;
  border-radius: 3px;
  background: linear-gradient(to top left, #65e49b, #65e4b0);
  font-family: 'Roboto Slab', serif;
  font-size: 1.125rem;
  letter-spacing: 0.045rem;
  color: #ffffff;
  padding-left: 1.7rem;
  padding-right: 1.7rem;
  padding-top: 0.9rem;
  padding-bottom: 0.9rem;
  cursor: pointer;
  box-shadow: 0 -2px #b2f1be inset;
  @media (max-width: 25rem) {
    font-size: 0.8rem;
  }
}

.btn--small {
  padding-left: 2.65rem;
  padding-right: 2.65rem;
}

.btn--buy {
  background: linear-gradient(to top left, #65e49b, #65e4b0);
  box-shadow: 0 -2px #59bb88 inset;
}

.btn--email {
  padding-left: 15rem;
  padding-right: 15rem;
  @media (max-width: 36rem) {
    padding-left: 11.5rem;
    padding-right: 11.5rem;
  }
  @media (max-width: 30rem) {
    padding-left: 7rem;
    padding-right: 7rem;
  }
}

.btn:focus {
  outline: none;
}

input {
  width: 448px;
  padding-top: 14px;
  padding-bottom: 14px;
  padding-left: 25px;
  padding-right: 100px;
  letter-spacing: 0.08rem;
  color: #fff;
  background-color: #44444f;
  border-radius: 5px;
  border: none;
  @media (max-width: 48rem) {
    padding-right: 25px;
  }
  @media (max-width: 36rem) {
    padding-right: 25px;
    width: 350px;
  }
  @media (max-width: 30rem) {
    padding-right: 10px;
    width: 220px;
  }
  @media (max-width: 25rem) {
    padding-right: 10px;
    width: 180px;
    padding-top: 12px;
    padding-bottom: 12px;
  }
}

input:focus {
  outline: none;
}

.cont {
  width: 100%;
  max-width: 1170px;
  padding-left: 15px;
  padding-right: 15px;
  margin-inline-start: 30px;
  margin-inline-end: 30px;
}

@media (max-width: 75rem) {
  .cont {
    margin: 0, auto;
    max-width: 69.375rem;
  }
}

@media (max-width: 62rem) {
  .cont {
    max-width: 57rem;
    margin: 0, auto;
  }
}

@media (max-width: 48rem) {
  .cont {
    max-width: 43.125rem;
    margin: 0, auto;
  }
}

@media (max-width: 36rem) {
  .cont {
    max-width: 29rem;
    margin: 0, auto;
  }
}

@media (max-width: 30rem) {
  .cont {
    max-width: 23rem;
    margin: 0, auto;
  }
}
  @media screen and (max-width: 25rem) {
    .cont {
      max-width: 20rem;
      margin: 0, auto;
    }
}


// Header
.header {
  width: 100%;
  height: auto;
  background-image: url(../img/bg-header.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
 
    &__items {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-top: 40px; 
    }

    // Burger
    &__burger {
      background-color: #4f9d98;
      position: relative;
      width: 40px;
      height: 30px;
      z-index: 3;
      margin-right: 50px;
      display: none;
      @media (max-width: 62rem){
        display: block;
        position: fixed;
        right: 10px;
        top: 37px;  
      }
      @media (max-width: 36rem){
        margin-right: 0px;
      }
      &>span {
        position: absolute;
        background-color: #fff;
        width: 80%;
        height: 2px;
        top: 14px;
        left: 4px;
        transition: all 0.3s ease 0s;
      }
    }

    &__burger::before,
    &__burger::after {
      content: '';
      background-color: #fff;
      position: absolute;
      width: 80%;
      height: 2px;
      left: 4px;
      transition: all 0.3s ease 0s;
    }

    &__burger::before {
      top: 3px;
    }
    &__burger::after {
      bottom: 3px;
    }

    &__burger.active>span {
      transform: scale(0);
    }
    &__burger.active::before {
      transform: rotate(45deg);
      top: 13.5px;
    }
    &__burger.active::after {
      transform: rotate(-45deg);
      bottom: 13.5px;
    }

    &__burger.fixed {
      display: block;
      position: fixed;
      right: 10px;
      top: 37px; 
    }
    
    // Burger

    &__menu {
      width: 100%;
      max-width: 800px;
      height: auto;
      margin-right: -80px;
      @media (max-width: 62rem) {
      display: none;
    }
    }
    
    &__link {
      color: #ffffff;
      font-family: 'Open Sans', sans-serif;
      font-size: 1rem;
      font-weight: 400;
      padding-left: 18px;
      line-height: 2rem;
      letter-spacing: 0.03rem;
      position: relative;
     
    }

    &__link::before {
      content: '';
      position: absolute;
      width: 70%;
      height: 3px;
      background-color: #65e496;
      bottom: -3px;
      opacity: 0;
      @media (max-width: 62rem) {
        bottom: 2px;
      }
    }

    &__link:hover:before {
      opacity: 1;
    }

    &__link.active::before {
      opacity: 1;
    }

    &__menu.active {
      position: fixed;
      display: flex;
      flex-direction: column;
      width: 100%;
      max-width: 190px;
      height: auto;
      top: 20px;
      right: 130px;
      padding-left: 0;
      padding-bottom: 30px;
      background-color: #4f9d98;
      z-index: 2;
      @media (max-width: 36rem) {
        right: 85px;
      }
    }

    &__content {
      margin-top: 166px;
      padding-bottom: 158px;
      display: flex;
      justify-content: start;
      @media (max-width: 62rem) {
        margin-top: 100px;
        padding-bottom: 100px;
      }
      @media (max-width: 48rem) {
        margin-top: 50px;
        padding-bottom:50px;
        flex-direction: column;
        align-items: center;
      }
    }

    &__cover {
      display: block;
      width: 100%;
      max-width: 445px;
      height: auto;
      position: relative;
      @media (max-width: 48rem) {
        width: 300px;
        padding-bottom: 30px;
      }
      @media (max-width: 30rem) {
        width: 250px;
        padding-bottom: 25px;
      }
    }

    &__cover--image {
      width: 100%;
      max-width: 445px;
      height: auto;
      &>img {
        @media (max-width: 48rem) {
          width: 350px;
          height: auto;
        }
        @media (max-width: 30rem) {
          width: 250px;
          height: auto;
        }
      }
    }

    &__name--book {
      position: absolute;
      font-family: 'Roboto Slab', serif;
      font-size: 2.25rem;
      font-weight: 700;
      letter-spacing: 0.058rem;
      text-transform: uppercase;
      color: #ffffff;
      top: 110px;
      left: 130px;
      @media (max-width: 48rem) {
        font-size: 1.8rem;
        left: 110px;
      }
      @media (max-width: 30rem) {
        font-size: 1.4rem;
        left: 75px;
        top: 60px;
      }
    }

    &__about--book {
      position: absolute;
      font-family: 'Open Sans', sans-serif;
      font-size: 0.875rem;
      font-weight: 400;
      letter-spacing: 0.042rem;
      color: #65e496;
      top: 155px;
      left: 130px;
      @media (max-width: 48rem) {
        left: 110px;
      }
      @media (max-width: 30rem) {
        left: 55px;
        top: 100px;
        font-size: 0.7rem;
      }  
    }

    &__author--book {
      position: absolute;
      font-family: 'Open Sans', sans-serif;
      font-size: 0.875rem;
      font-weight: 400;
      letter-spacing: 0.042rem;
      color: #65e496;
      bottom: 60px;
      left: 85px;
      @media (max-width: 30rem) {
        left: 55px;
        font-size: 0.7rem;
      }  
    }

    &__presentation--book {
      width: 100%;
      max-width: 580px;
      margin-left: 40px;
      padding-left: 15px;
      padding-right: 15px;
      @media (max-width: 25rem) {
        margin-left: 0;
      }
    }

    &__presentation--author {
      font-family: 'Open Sans', sans-serif;
      font-size: 1.125rem;
      letter-spacing: 0.056rem;
      font-weight: 400;
      color: #ffffff;
    }

    &__presentation--name {
      letter-spacing: 0.1rem;
      color: #ffffff;
      padding-top: 36px;
      padding-bottom: 5px;
    }

    &__presentation--about {
      font-family: 'Roboto Slab', serif;
    }

    &__presentation--text {
      font-family: 'Open Sans', sans-serif;
      letter-spacing: 0.045rem;
      color: #ffffff;
      padding-top: 27px;
    }

    &__btn {
      padding-top: 45px;
      padding-bottom: 20px;
    }  
}



