// Slider
.slider {
  width: 100%;
  height: auto;
  background-color: #44444f;

  &__carousel {
     width: 100%;
    height: auto;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    padding-top: 230px;
    padding-bottom: 208px;
    @media (max-width: 62rem) {
      padding-top: 100px;
      padding-bottom: 100px;
    }
  }

  &__photo {
    text-align: -webkit-center;
    &>img {
      position: relative;
    }
  }
  

  &__quotes {
    font-size: 3rem;
    color: #65e496;
    position: absolute;
    top: -9px;
    margin-left: 43px;
  }

  &__name {
    font-family: 'Roboto Slab', serif;
    font-size: 1.5rem;
    letter-spacing: 0.05rem;
    font-weight: 700;
    color: #65e496;
    padding-top: 20px;
  }

  &__prof {
    color: #9191b5;
    padding-top: 5px;
    padding-bottom: 32px;
  }

  &__text {  
    width: 100%;
    max-width: 750px;
    margin-left: auto;
    margin-right: auto;
    color: #9191b5;
    line-height: 1.9;
  }
}

.slick-dots {
  display: flex;
  justify-content: center;
  list-style-type: none;
  padding: 0;
  padding-top: 45px;
  &>li {
    margin: 0 5px;
    &>button {
      text-indent: -9999px;
      width: 8px;
      height: 8px;
      border: none;
      background-color: #65e496;
      line-height: 0;
      font-size: 0;
      padding: 5px;
      cursor: pointer;
      border-radius: 50%;
      &:focus {
        outline: none;
      }
    } 
  }
  &>li.slick-active {
    &>button {
      width: 37px;
      border-radius: 10px;
    }
  
}
}



