    background-size: cover;
    position: relative;
    @media (max-width: 62rem) {
      max-width: 335px;
      height: 505px;
    }
    @media (max-width: 30rem) {
      max-width: 300px;
      height: 450px;
    }
    @media (max-width: 25rem) {
      max-width: 300px;
      height: 400px;
      background-image: none;
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: auto;
    top: 50px;
    left: 50px;
    @media (max-width: 30rem) {
      left: 26px;
    }
    @media (max-width: 30rem) {
      left: 0;
    }
    &>img {
      @media (max-width: 62rem) {
        width: 300px;
      }
      @media (max-width: 30rem) {
        width: 250px;
      }
    }
  }

  &__item {
    width: 100%;
    max-width: 570px;
    height: auto;
    @media (max-width: 62rem) {
      max-width: 500px;
      padding-right: 30px;
      @media (max-width: 48rem) {
        padding-right: 0;
        padding-top: 30px;
      }
    }
  }

  &__author {
    font-family: 'Open Sans', sans-serif;
    font-size: 1.125rem;
    font-weight: 400;
    letter-spacing: 0.07rem;
    color: #696980;
    padding-bottom: 23px;
  }

  &__about {
    margin-top: 4px;
    letter-spacing: 0.067rem;
  }

  &__event {
    margin-top: 34px;
    padding-bottom: 26px;
    @media (max-width: 48rem){
      padding-bottom: 10px;
      margin-top: 20px;
    }
  }

  &__data {
    font-family: 'Open Sans', sans-serif;
    font-size: 0.94rem;
    font-weight: 400;
    letter-spacing: 0.045rem;
    color: #696980;
    padding-bottom: 9px;
  }
  
  &__place {
    font-family: 'Roboto Slab', serif;
    font-size: 1.125rem;
    letter-spacing: 0.054rem;
    font-weight: 700;
    color: #44444f;
    padding-bottom: 19px;
  }

  &__text {
    padding-bottom: 22px;
  }
}


// Blog
.blog {
  background-color: #f5f6fa;

  &__title {
    padding-top: 156px;
    margin-bottom: 70px;
    text-align: center;
    @media (max-width: 62rem) {
      padding-top: 56px;
      margin-bottom: 50px;
    }
  }

  &__name {
    padding-bottom: 5px;
  }

  &__items {
    display: flex;
    justify-content: space-between;
    @media (max-width: 62rem) {
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
  }

  &__item {
    width: 100%;
    height: auto;
    @media (max-width: 75rem) {
      width: 32%;
    }
    @media (max-width: 62rem) {
      width: 100%;
      padding-bottom: 50px;
    }
  }

  &__image {
    padding-bottom: 20px;
    @media (max-width: 30rem) {
      width: 100%;
      max-width: 300px;
    }
  }

  &__under {
    font-family: 'Roboto Slab', serif;
    font-size: 1.125rem;
    letter-spacing: 0.054rem;
    font-weight: 700;
    padding-bottom: 5px;
  }

  &__author {
    padding-bottom: 10px;
    &>span {
      color: #65e49d;
    }
  }

  &__text {
    @media (max-width: 62rem) {
      padding-left: 200px;
      padding-right: 200px;
    }
    @media (max-width: 36rem) {
      padding-left: 0;
      padding-right: 0;
    }
  }

  &__btn {
    text-align: center;
    margin-top: 83px;
    padding-bottom: 160px;
    @media (max-width: 62rem) {
      margin-top: 5px;
    padding-bottom: 50px;
    }
  }
}


// NewsLetter
.newsletter {
  background-color: #fff;

  &__items {
    margin-top: 160px;
    margin-bottom: 155px;
    display: flex;
    justify-content: space-between;
    @media (max-width: 62rem) {
      flex-direction: column;
      align-items: center;
      text-align: center;
      margin-top: 60px;
      margin-bottom: 55px;
    }
  }

  &__item {
    width: 100%;
    max-width: 580px;
    height: auto;
    @media (max-width: 62rem) {
      padding-bottom: 30px;
    }
  }

  &__name {
    padding-bottom: 12px;
  }

  &__subscribe {
    width: 100%;
    max-width: 570px;
    @media (max-width: 48rem) {
    
    }
  }

  &__btn{
    margin-top: 23px; 
  }
}

// Footer
.footer {
  background-color: #44444f;

  &__items {
    display: flex;
    justify-content: space-between;
    padding-top: 160px;
    padding-bottom: 155px;
    color: #9191b5;
    @media (max-width: 62rem) {
      padding-top: 60px;
      padding-bottom: 55px;
      flex-wrap: wrap;
    }
    @media (max-width: 62rem) {
      padding-top: 30px;
    }
  }

  &__item {
    @media (max-width: 62rem) {
      margin-top: 20px;
    }
  }

  &__item.about {
    width: 100%;
    max-width: 400px;
    @media (max-width: 62rem) {
      max-width: 100%;
    }
  }

  &__text {
    padding-top: 23px;
    @media (max-width: 62rem) {
      padding-top: 10px;
    }
  }

  &__item.links {
    width: 100%;
    max-width: 250px;
    padding-top: 5px;
    @media (max-width: 62rem) {
      padding-left: 8px;
    }

    &>h2 {
      font-family: 'Roboto Slab', serif;
      font-size: 1.125rem;
      letter-spacing: 0.054rem;
      font-weight: 700;
      color: #65e496;
      text-transform: uppercase;
      padding-top: 5px;
    }
  }

  &__lists {
    padding-top: 16px;
    @media (max-width: 62rem) {
      padding-top: 8px;
    }

    &>li {
      padding-top: 10px;
      @media (max-width: 62rem) {
        padding-top: 5px;
      }
    }
  }

  &__list {
    font-family: 'Open Sans', sans-serif;
    font-size: 0.94rem;
    font-weight: 400;
    letter-spacing: 0.045rem;
    color: #9191b5;
  }

  &__item.contact {
    width: 100%;
    max-width: 260px;
    margin-right: 10px;
    margin-left: 52px;
    @media (max-width: 62rem) {
      margin-left: 8px;
      margin-right: 0;
    }

    &>h2 {
      font-family: 'Roboto Slab', serif;
      font-size: 1.125rem;
      letter-spacing: 0.054rem;
      font-weight: 700;
      color: #65e496;
      text-transform: uppercase;
      padding-top: 10px;
    }
    &>p {
      padding-top: 22px;
      @media (max-width: 62rem) {
        padding-top: 8px;
      }
    }
  }

  &__social--icons {
    display: flex;
    justify-content: flex-start;
    padding-top: 15px;
  }

  &__icon {
    color: #65e496;
    padding-right: 11px;
  }
}
